@import '../../scss/variables';

header {
  width: 100%;
  height: $nav-scroll-height;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 6%;
  box-sizing: border-box;

  img {
    pointer-events: none;
    user-select: none;
  }

  @media #{$mq-small} {
    padding: 0 6%;
  }

  @media #{$mq-medium} {
    padding: 0 8%;
  }

  @media #{$mq-large} {
    padding: 0 10%;
  }
}

#nav, #nav-mobile {
  ol {
    padding: 0;
    margin: 0;
    list-style: none;

    li {
      position: relative;
      counter-increment: item 1;

      a {
        font-family: $code-font;
        color: $primary-color;
        text-decoration: none;
        transition: color 0.25s ease;

        &:hover {
          color: $code-color;
        }

        &::before {
          content: attr(value) ".";
          color: $code-color;
        }
      }
    }
  }
}

#nav {
  display: none;

  @media #{$mq-xsmall} {
		display: block;
  }

  ol {
    display: flex;
    justify-content: space-between;
    align-items: center;

    li {
      margin: 0 5px;

      a {
        padding: 10px;

        &::before {
          margin-right: 5px;
          text-align: right;
          font-size: 14px;
        }
      }
    }
  }
}

#nav-mobile {
  display: block;

  @media #{$mq-xsmall} {
		display: none;
  }
  
  ol {
    width: 100%;

    li {
      margin: 0 auto 10px;

      a {
        display: inline-block;
        position: relative;
        width: 100%;
        padding: 3px 20px 20px;
        box-sizing: border-box;
        font-size: 1.2em;

        &::before {
          display: block;
          margin-bottom: 5px;
        }
      }
    }
  }

  button {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin-right: -10px;
    padding: 15px;
    text-transform: none;
    transition-timing-function: linear;
    transition-delay: 0.15s;
    transition-property: opacity, filter;
    color: $code-color;
    border: none;
    z-index: 10;
  }

  aside {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    padding: 50px 10px;
    width: calc(min(75vw, 400px));
    height: 100vh;
    background-color: $nav-aside-color;
    z-index: 5;
    visibility: hidden;
    transform: translateX(100vw);
    transition: all 0.25s cubic-bezier(0.645,0.045,0.355,1);
    box-sizing: border-box;

    &.visible {
      visibility: visible;
      transform: translateX(0);
    }

    nav {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      flex-direction: column;
      text-align: center;
    }
  }
}

.hamburger-box--wrapper {
  display: inline-block;
  position: relative;
  width: 30px;
  height: 24px;
}

.hamburger-box {
  position: absolute;
  top: 50%;
  right: 0;
  width: 30px;
  height: 2px;
  border-radius: 4px;
  background-color: $code-color;
  transition: transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19) 0s;
  transform: rotate(0deg);

  &::before, &::after {
    content: "";
    display: block;
    position: absolute;
    left: auto;
    right: 0;
    height: 2px;
    border-radius: 4px;
    background-color: $code-color;
    transition-timing-function: ease;
    transition-delay: 0.15s;
    transition-property: transform;
  }

  &.active {
    transition: transform 0.22s cubic-bezier(0.215, 0.61, 0.355, 1) 0.12s;
    transform: rotate(225deg);

    &::before {
      width: 100%;
      top: 0;
      opacity: 0;
      transition: top 0.1s ease-out, opacity 0.1s ease-out 0.12s;
    }

    &::after {
      width: 100%;
      bottom: 0;
      transform: rotate(-90deg);
      transition: bottom 0.1s ease-out, transform 0.22s cubic-bezier(0.215,0.61,0.355,1) 0.12s;
    }
  }

  &::before {
    width: 120%;
    top: -10px;
    opacity: 1;
    transition: top 0.1s ease-in 0.25s, opacity 0.1s ease-in;
  }

  &::after {
    width: 80%;
    bottom: -10px;
    transform: rotate(0deg);
    transition: bottom 0.1s ease-in 0.25s, transform 0.22s cubic-bezier(0.55,0.055,0.675,0.19);
  }
}

.headroom {
  background-color: rgba($background, 0.85);
  box-shadow: 0px 4px 9px -2px #121310;
  backdrop-filter: blur(4px);
  transition: box-shadow 0.25s ease-in-out; 
  transition: padding 0.25s ease-in-out; 
}

.headroom--unpinned, .headroom--unfixed {
  box-shadow: none;
}

.force-visible {
  .headroom--unpinned {
    transform: translate3d(0px, 0px, 0px) !important;
    box-shadow: 0px 4px 9px -2px #121310;
  }

  .headroom--unfixed {
    position: fixed !important;
  }
}

.headroom--unfixed {
  padding-top: 1em;
}
