@import 'scss/variables';

main {
  margin: 0 6%;

  @media #{$mq-small} {
    margin: 0 6%;
  }

  @media #{$mq-medium} {
    margin: 0 8%;
  }

  @media #{$mq-large} {
    margin: 0 10%;
  }
}

body.blur main > * {
  filter: blur(5px) brightness(0.7);
  transition: all 0.25s cubic-bezier(0.645,0.045,0.355,1);
  pointer-events: none;
  user-select: none;
}

section {
  padding: 20% 5px;
  overflow: hidden;

  @media #{$mq-xsmall} {
    padding: 15% 0;
  }

  @media #{$mq-medium} {
    padding: 10% 0;
  }

  &:first-child {
    padding-top: 40%;

    @media #{$mq-xsmall} {
      padding-top: 20%;
    }
  
    @media #{$mq-medium} {
      padding-top: 12%;
    }
  }
}

span {
  &.code {
    font-family: $code-font;
    font-size: 18px;
    font-weight: 300;
    color: $code-color;

    @media #{$mq-xsmall} {
      font-size: 20px;
    }
  }
}

h2 span {
  margin-right: 0.5em;
}