@import '../../scss/variables';

section#welcome div.content {
  margin-block-end: 15%;

  @media #{$mq-xsmall} {
    max-width: 75%;
    margin-block-end: 6%;
	}

  @media #{$mq-small} {
    max-width: 65%;
    margin-block-end: 6%;
	}

  @media #{$mq-medium} {
    max-width: 50%;
    margin-block-end: 4%;
	}
}